.header {
   padding: 40px 0 0;
	position: relative;
	background: #1c2839;
	width: 100%;
}

.navBar {
	width: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	text-align: center;
	margin: 40px 0 0;
}

.navButton {
	text-transform: uppercase;
	color: white;
	padding: 20px 0;
	width: 12.5%;
	background: #3f4e63;
	border: none;
	border-right: 2px solid #f0f0f0;
	-webkit-transition: 0.25s;
	-o-transition: 0.25s;
	transition: 0.25s;
	font: inherit;
	font-size: 1.2em;
}

.navButton:first-of-type {
	border-top-left-radius: 4px;
}

.navButton:last-of-type {
	border-top-right-radius: 4px;
	border-right: none;
}

.navButton:hover {
	cursor: pointer;
	-webkit-box-shadow: inset 0 0 100px 100px rgba(255, 255, 255, 0.1);
	        box-shadow: inset 0 0 100px 100px rgba(255, 255, 255, 0.1);
}

.gradient {
   background-image: -webkit-radial-gradient(circle, #e1e8f0, #646a73);
   background-image: -o-radial-gradient(circle, #e1e8f0, #646a73);
   background-image: radial-gradient(circle, #e1e8f0, #646a73);
}

.img-container {
   width: 7.5%;
   margin: auto;
}

.img-container img {
   width: 100%;
}

.active {
	color: red;
}


